import React, { useState, useEffect } from 'react';
import { Pie } from 'react-chartjs-2';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import useDebounce from './hooks/useDebounce';
import './App.css';
import ChartDataLabels from 'chartjs-plugin-datalabels';

ChartJS.register(ArcElement, Tooltip, Legend);

const generateColors = (numColors, isBorder = false) => {
  const baseColors = [];
  const borderColors = [];
  const hueStep = 360 / numColors;

  for (let i = 0; i < numColors; i++) {
    const hue = i * hueStep;
    baseColors.push(`hsl(${hue}, 70%, 80%)`);
    borderColors.push(`hsl(${hue}, 70%, 50%)`);
  }

  return isBorder ? borderColors : baseColors;
};

function App() {
  const [expenses, setExpenses] = useState([]);
  const [category, setCategory] = useState('');
  const [amount, setAmount] = useState('');
  const [remark, setRemark] = useState('');
  const [applyFilters, setApplyFilters] = useState(false);
  const [filterMonth, setFilterMonth] = useState('');
  const [filterYear, setFilterYear] = useState('');
  const [filterCategory, setFilterCategory] = useState('');
  const [filterRemark, setFilterRemark] = useState('');
  const [chartData, setChartData] = useState({
    labels: [],
    datasets: [
      {
        label: 'Expenses by Category',
        data: [],
        backgroundColor: generateColors(3),
        borderColor: generateColors(3, true),
        borderWidth: 1,
      },
    ],
  });

  const accountId = process.env.REACT_APP_MY_ACCOUNT_ID;
  const conversionRate = process.env.REACT_APP_CONVERSION_RATE || 1;

  const options = {
    plugins: {
      legend: {
        labels: {
          color: '#008080',
          font: { size: 20 },
        },
      },
      tooltip: {
        callbacks: {
          label: (tooltipItem) => {
            const total = tooltipItem.dataset.data.reduce((acc, val) => acc + val, 0);
            const percentage = ((tooltipItem.raw / total) * 100).toFixed(2);
            return `${tooltipItem.label}: ${tooltipItem.raw} (${percentage}%)`;
          },
        },
        bodyFont: { size: 26 },
        titleFont: { size: 30 },
        backgroundColor: '#333333',
        titleColor: '#ffffff',
        bodyColor: '#ffffff',
      },
    },
  };

  const debouncedFilterMonth = useDebounce(filterMonth, 500);
  const debouncedFilterYear = useDebounce(filterYear, 500);
  const debouncedFilterCategory = useDebounce(filterCategory, 500);
  const debouncedFilterRemark = useDebounce(filterRemark, 500);

  useEffect(() => {
    document.title = 'Hedgetrack';
    if (applyFilters) {
      loadExpensesFromBackend();
      setApplyFilters(false);
    }
  }, [applyFilters, debouncedFilterMonth, debouncedFilterYear, debouncedFilterCategory, debouncedFilterRemark]);

  const loadExpensesFromBackend = async () => {
    try {
      // Build the query with filters, allowing empty values for reset
      const query = new URLSearchParams({
        month: debouncedFilterMonth || '',   // Reset if blank
        year: debouncedFilterYear || '',     // Reset if blank
        category: debouncedFilterCategory || '',
        remark: debouncedFilterRemark || '',
        accountId
      });

      // Fetch the data from the backend
      const response = await fetch(`http://localhost:3001/fetch-combined-data?${query.toString()}`);
      if (!response.ok) {
        throw new Error(`Network response was not ok: ${response.statusText}`);
      }
      let data = await response.json();
      console.log('Fetched data:', data);

      // Filter out "Crypto" if it does not match the applied filters
      if (debouncedFilterCategory || debouncedFilterRemark || debouncedFilterMonth || debouncedFilterYear) {
        data = data.filter(expense => expense.category !== 'Crypto' || matchesFilters(expense));
      }

      // Sort data by timestamp in descending order (latest first)
      const sortedData = data.sort((a, b) => b.consensusTimestamp.localeCompare(a.consensusTimestamp));

      // Set expenses and update the chart data
      setExpenses(sortedData);
      updateChartData(sortedData);

      // Reset apply filters flag
      setApplyFilters(false);
    } catch (error) {
      console.error('Error fetching data:', error);
      alert(`Failed to fetch data: ${error.message}`);
      setApplyFilters(false);
    }
  };

  // Helper function to check if an expense matches the applied filters
  const matchesFilters = (expense) => {
    const date = new Date(expense.consensusTimestamp); // Ensure it parses correctly here
    const matchesMonth = !debouncedFilterMonth || (date.getMonth() + 1 === parseInt(debouncedFilterMonth));
    const matchesYear = !debouncedFilterYear || (date.getFullYear() === parseInt(debouncedFilterYear));
    const matchesCategory = !debouncedFilterCategory || expense.category === debouncedFilterCategory;
    const matchesRemark = !debouncedFilterRemark || expense.remark.includes(debouncedFilterRemark);
  
    return matchesMonth && matchesYear && matchesCategory && matchesRemark;
  };  
  
  const addExpense = async () => {
    const parsedAmount = parseFloat(amount);

    if (!category || isNaN(parsedAmount) || parsedAmount <= 0) {
      alert('Please provide a valid category and amount greater than 0.');
      return;
    }

    const newExpense = { category, amount: parsedAmount, remark };
    const updatedExpenses = [...expenses, newExpense];
    setExpenses(updatedExpenses);
    await logTransactionOnBackend(newExpense);
    updateChartData(updatedExpenses);

    setCategory('');
    setAmount('');
    setRemark('');
  };

  const logTransactionOnBackend = async (expenseData) => {
    try {
      const response = await fetch(`http://localhost:3001/add-expense`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(expenseData),
      });

      if (!response.ok) {
        throw new Error(`Network response was not ok: ${response.statusText}`);
      }

      console.log('Expense logged successfully');
    } catch (error) {
      console.error('Error logging expense:', error);
      alert(`Failed to log expense: ${error.message}`);
    }
  };

  const updateChartData = (data) => {
    const categoryTotals = data.reduce((acc, item) => {
      const category = item.category || 'Other';
      if (!acc[category]) {
        acc[category] = 0;
      }
      acc[category] += parseFloat(item.amount);
      return acc;
    }, {});

    const labels = Object.keys(categoryTotals);
    const dataPoints = Object.values(categoryTotals);
    const backgroundColors = generateColors(labels.length);
    const borderColors = generateColors(labels.length, true);

    setChartData({
      labels,
      datasets: [{
        label: 'Expenses by Category',
        data: dataPoints,
        backgroundColor: backgroundColors,
        borderColor: borderColors,
        borderWidth: 1,
      }],
    });
  };

  return (
    <div className="App">
      <h1>Expense Tracker</h1>
      <div className="filters">
        <input
          type="text"
          placeholder="Category"
          value={filterCategory}
          onChange={(e) => setFilterCategory(e.target.value)}
        />
        <input
          type="text"
          placeholder="Remark"
          value={filterRemark}
          onChange={(e) => setFilterRemark(e.target.value)}
        />
        <input
          type="number"
          placeholder="Month (1-12)"
          value={filterMonth}
          min="1" 
          max="12"
          onChange={(e) => setFilterMonth(e.target.value)}
        />
        <input
          type="number"
          placeholder="Year (YYYY)"
          value={filterYear}
          min="1970"
          onChange={(e) => setFilterYear(e.target.value)}
        />
        <button onClick={() => setApplyFilters(true)}>Apply Filters</button>
      </div>
      <div className="expense-form">
        <input
          type="text"
          placeholder="Category"
          value={category}
          onChange={(e) => setCategory(e.target.value)}
        />
        <input
          type="number"
          placeholder="Amount"
          value={amount}
          onChange={(e) => setAmount(e.target.value)}
        />
        <input
          type="text"
          placeholder="Remark"
          value={remark}
          onChange={(e) => setRemark(e.target.value)}
        />
        <button onClick={addExpense}>Add Expense</button>
      </div>
      <div className="chart-container">
        <Pie data={chartData} options={options} plugins={[ChartDataLabels]} />
      </div>

      <div className="expense-table">
        <table>
          <thead>
            <tr>
              <th>Timestamp</th>
              <th>Category</th>
              <th>Amount</th>
              <th>Remark</th>
            </tr>
          </thead>
          <tbody>
            {expenses.map((expense, index) => (
              <tr key={index}>
                <td>{expense.consensusTimestamp}</td>
                <td>{expense.category}</td>
                <td>{parseFloat(expense.amount) || 0.0000.toFixed(4)}</td>
                <td>{expense.remark}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default App;
